import { FileRowType, IndexStatus, ServerFile } from "@rp/common/utils"

function createServerFile(
  data: IndexStatus,
  name: FileRowType["name"],
  id: FileRowType["id"],
  shared_with: FileRowType["sharedWith"],
  owner: FileRowType["owner"],
  cacheSize: number,
  cacheTs: string,
): ServerFile {
  return {
    folder_id: null,
    error: null,
    tags: null,
    trashed: false,
    status: data.status === "INDEXED" ? "COMPLETED" : "FAILED",
    file_name: name,
    file_id: id,
    raw_size: cacheSize,
    upload_ts: cacheTs,
    shared_with,
    owner,
  }
}

export { createServerFile }
